.small-font-uppercase {
  font-size: 75%;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.medium-font-uppercase {
  .small-font-uppercase();
  font-size: 85%;
}

.margin-full-horizontal {
  margin-left: -@margin-full;
  margin-right: -@margin-full;
  padding-left: @padding-full;
  padding-right: @padding-full;
}

.margin-full-vertical {
  margin-top: -@margin-full;
  margin-bottom: -@margin-full;
  padding-top: @padding-full;
  padding-bottom: @padding-full;
}

.margin-double-horizontal {
  margin-left: -@margin-double;
  margin-right: -@margin-double;
  padding-left: @padding-double;
  padding-right: @padding-double;
}

.margin-double-vertical {
  margin-top: -@margin-double;
  margin-bottom: -@margin-double;
  padding-top: @padding-full;
  padding-bottom: @padding-full;
}

.margin-top-full {
  margin-top: @margin-full;
}

.margin-bottom-full {
  margin-bottom: @margin-full;
}

.padding-top-full {
  margin-top: @padding-full;
}

.padding-bottom-full {
  margin-bottom: @padding-full;
}
