.main {
  
  .tiled-gallery img {
    background: none;
    border: 0;
    border-radius: none;
    padding: none;
    box-shadow: none;
  }
  

  .postthumb {
    position: relative;
  }
}


.edit-link {
  font-size: 60%;
  position: relative;
  right: -9px;
  top: -3px;
}


&.has-featured-image .edit-link {
  color: #fff;
}


.post-meta {
  .small-font-uppercase();
  background: @post-meta-bg;
  border-bottom: @post-meta-border-bottom;
  margin: @post-meta-margin;
  max-width: inherit;
  padding: @post-meta-padding;
  position: relative;
  
  span {
    margin-right: 5px;
  }
}
