// Variables
// @alert-success-bg
// @alert-success-color
// @alert-error-bg
// @alert-error-color
// @alert-message-bg
// @alert-message-color
// @alert-padding
// @alert-margin
// @alert-font-weight
ul.button-nav {
  li.current {
    a {
      font-weight: bold;
    }
  }
  
li {
    float: left;
    margin: 0 10px 10px 0;
    list-style: none;
  }
}

div#message.updated {
  clear: both;
  p {
    background-color: @alert-success-bg;
    border: 1px solid darken(@alert-success-bg, 5%);
    color: @alert-success-color;
    font-weight: @alert-font-weight;
  }
}

div.accept,
div.reject {
  float: left;
  margin-left: 10px;
}

div#message {
  margin-top: @margin-full;
  margin-bottom: @margin-full;
}

#message.info {
  margin-bottom: 0;
}

.standard-form#signup_form {
  div {
    div.error {
      background: @alert-error-bg;
      color: @alert-error-color;
      margin: 0 0 10px;
      padding: 6px;
      width: 90%;
      font-weight: @alert-font-weight;
    }
  }
}

div#message {
  p {
    background-color: @alert-message-bg;
    border: 1px solid darken(@alert-message-bg, 5%);
    clear: left;
    color: @alert-message-color;
    font-weight: @alert-font-weight;
  }
}

#sitewide-notice.admin-bar-on {
  #message {
    top: 3.3em;
  }
}

div#message p,
#sitewide-notice p {
  font-size: 90%;
  display: block;
  padding: @padding-half;
}

#sitewide-notice {
  strong {
    display: block;
    margin-bottom: -1em;
  }
  
#message {
    left: 2%;
    position: fixed;
    top: 1em;
    width: 96%;
    z-index: 9999;
    font-weight: @alert-font-weight;
  }
}
