.post-loop {
  .hentry {
    margin-left: -@margin-full;
    margin-right: -@margin-full;
    padding-left: @padding-full;
    padding-right: @padding-full;
    padding-bottom: (@padding-full * 2);
    
    .read-more-link {
      display: block;
      float: right;
      margin: @margin-full @margin-half 0 0;
    }
    

    .postthumb {
      
      //margin-top: 0;
    }
    

    h2.entry-title {
      font-size: @font-size-h3;
    }
  }
  

  .entry-title {
    background: @loop-header-background;
    
    a {
      color: @loop-header-link-color;
      
      &:hover {
        color: @loop-header-link-color-hover;
      }
    }
  }
  

  .intro-paragraph {
    border-bottom: 1px solid darken(@white, 5%);
    margin-bottom: @margin-half;
    padding-bottom: @padding-full;
  }
}
